 0.1)',\r\n                borderColor: '#2592f6', // '#99CCFF',\r\n                borderWidth: 2\r\n              },\r\n              emphasis: {\r\n                areaColor: '#2592f6'\r\n              }\r\n            }\r\n          },\r\n          series: [\r\n            // {\r\n            //   name: '地图',\r\n            //   type: 'map',\r\n            //   roam: true,\r\n            //   mapType: mapName,\r\n            //   geoIndex: 0,\r\n            //   label: {\r\n            //     normal: {\r\n            //       textStyle: {\r\n            //         fontSize: 10,\r\n            //         fontWeight: 'bold',\r\n            //         color: '#2592f6'\r\n            //       }\r\n            //     }\r\n            //   },\r\n            //   aspectScale: 0.75,\r\n            //   // zoom: 3,\r\n            //   itemStyle: {\r\n            //     normal: {\r\n            //       label: { show: true },\r\n            //       areaColor: 'rgba(0, 0, 0, 0.1)',\r\n            //       borderColor: '#2592f6', // '#99CCFF',\r\n            //       borderWidth: 2\r\n            //     },\r\n            //     emphasis: {\r\n            //       areaColor: '#2592f6',\r\n            //       label: {\r\n            //         color: '#fff'\r\n            //       }\r\n            //     }\r\n            //   }\r\n            // },\r\n            {\r\n              name: '散点',\r\n              type: 'effectScatter',\r\n              coordinateSystem: 'geo',\r\n              legendHoverLink: true,\r\n              // zoom: 3,\r\n              //   symbolSize: function(val) {\r\n              //     return Math.max(val[2] / 30, 8);\r\n              //   },\r\n              label: {\r\n                show: true,\r\n                offset: [10, -10], //偏移设置\r\n                fontSize: 12,\r\n                // lineHeight: 14,\r\n                // borderWidth: 1,\r\n                // backgroundColor: '#061a4c',\r\n                // borderColor: '#97b5f0',\r\n                // padding: 6,\r\n                formatter(value) {\r\n                  // console.log(value);\r\n                  return value.data.name;\r\n                },\r\n                color: '#fff'\r\n              },\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#f4ea2a', //标志颜色\r\n                  shadowBlur: 4,\r\n                  shadowColor: '#D8BC37'\r\n                }\r\n              },\r\n              data: tempData,\r\n              showEffectOn: 'render',\r\n              rippleEffect: {\r\n                brushType: 'stroke'\r\n              },\r\n              hoverAnimation: true,\r\n              zlevel: 1\r\n            }\r\n          ]\r\n        };\r\n        //\r\n        myChart.setOption(option1);\r\n        //\r\n        myChart.off('click');\r\n        //\r\n        myChart.on('click', params => {\r\n          console.log('点击', params);\r\n        });\r\n      },\r\n      // 数据处理(用于获取中心标注点)\r\n      dataDealFoo(geoJson) {\r\n        let list = geoJson.features;\r\n        // let obj = { name: '', pint: [] };\r\n        list.map(item => {\r\n          let arrX = [];\r\n          let arrY = [];\r\n          let arr = item.geometry.coordinates[0];\r\n          let name = item.properties.name;\r\n          arr.map(item => {\r\n            arrX.push(item[0]);\r\n            arrY.push(item[1]);\r\n          });\r\n          // console.log('arrx', arrX);\r\n          let x = (Math.min(...arrX) + Math.max(...arrX)) / 2;\r\n          let y = (Math.min(...arrY) + Math.max(...arrY)) / 2;\r\n          let result = gcoord.transform([x, y], gcoord.WGS84, gcoord.BD09);\r\n          // console.log('name', name, result);\r\n        });\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";