export default "<template>\r\n  <div :id=\"eleId\" class=\"chinaMapCls\" />\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  import chinaMapJson from '@/views/EchartUnit/map/mapJson/china/index.json';\r\n  export default {\r\n    props: {\r\n      data: {\r\n        type: Array,\r\n        default: () => [\r\n          [{ name: '黑龙江', value: 5 }],\r\n          [{ name: '内蒙古', value: 3 }],\r\n          [{ name: '北京市', value: 20 }],\r\n          [{ name: '吉林', value: 3 }],\r\n          [{ name: '辽宁', value: 5 }],\r\n          [{ name: '河北', value: 6 }],\r\n          [{ name: '天津市', value: 5 }],\r\n          [{ name: '山西', value: 7 }],\r\n          [{ name: '陕西', value: 6 }],\r\n          [{ name: '甘肃', value: 5 }],\r\n          [{ name: '宁夏', value: 5 }],\r\n          [{ name: '青海', value: 7 }],\r\n          [{ name: '新疆', value: 3 }],\r\n          [{ name: '西藏', value: 3 }],\r\n          [{ name: '四川', value: 10 }],\r\n          [{ name: '重庆市', value: 9 }],\r\n          [{ name: '广东', value: 10 }],\r\n          [{ name: '山东', value: 6 }],\r\n          [{ name: '河南', value: 8 }],\r\n          [{ name: '江苏', value: 10 }],\r\n          [{ name: '安徽', value: 9 }],\r\n          [{ name: '湖北', value: 10 }],\r\n          [{ name: '浙江', value: 16 }],\r\n          [{ name: '福建', value: 9 }],\r\n          [{ name: '湖南', value: 14 }],\r\n          [{ name: '贵州', value: 8 }],\r\n          [{ name: '广西', value: 7 }],\r\n          [{ name: '海南', value: 7 }],\r\n          [{ name: '上海市', value: 17 }]\r\n        ]\r\n      }\r\n    },\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`\r\n      };\r\n    },\r\n    watch: {\r\n      data: {\r\n        handler: function() {\r\n          this.initEchart();\r\n        },\r\n        deep: true\r\n      }\r\n    },\r\n    mounted() {\r\n      this.$echarts = echarts;\r\n      this.$nextTick(() => {\r\n        this.initEchart();\r\n      }, 0);\r\n    },\r\n    methods: {\r\n      initEchart() {\r\n        //\r\n        var chinaGeoCoordMap = {\r\n          黑龙江: [127.9688, 45.368],\r\n          内蒙古: [110.3467, 41.4899],\r\n          吉林: [125.8154, 44.2584],\r\n          北京市: [116.4551, 40.2539],\r\n          辽宁: [123.1238, 42.1216],\r\n          河北: [114.4995, 38.1006],\r\n          天津市: [117.4219, 39.4189],\r\n          山西: [112.3352, 37.9413],\r\n          陕西: [109.1162, 34.2004],\r\n          甘肃: [103.5901, 36.3043],\r\n          宁夏: [106.3586, 38.1775],\r\n          青海: [101.4038, 36.8207],\r\n          新疆: [87.9236, 43.5883],\r\n          西藏: [91.11, 29.97],\r\n          四川: [103.9526, 30.7617],\r\n          重庆市: [108.384366, 30.439702],\r\n          山东: [117.1582, 36.8701],\r\n          河南: [113.4668, 34.6234],\r\n          // 江苏: [118.8062, 31.9208],\r\n          江苏: [120.0062, 33.5208], // 调整后的\r\n          安徽: [117.29, 32.0581],\r\n          湖北: [114.3896, 30.6628],\r\n          浙江: [119.5313, 29.8773],\r\n          福建: [119.4543, 25.9222],\r\n          江西: [116.0046, 28.6633],\r\n          湖南: [113.0823, 28.2568],\r\n          贵州: [106.6992, 26.7682],\r\n          云南: [102.9199, 25.4663],\r\n          广东: [113.12244, 23.009505],\r\n          广西: [108.479, 23.1152],\r\n          海南: [110.3893, 19.8516],\r\n          上海市: [121.4648, 31.2891]\r\n        };\r\n\r\n        var chinaDatas = this.data;\r\n\r\n        // 只显示 北京市、天津市、河北、江苏、安徽、浙江、上海市、广东、湖南\r\n        var showProvinceArr = ['北京市', '天津市', '河北', '江苏', '安徽', '浙江', '上海市', '广东', '湖南'];\r\n        function showProvinceFoo(name, params) {\r\n          if (showProvinceArr.includes(name)) {\r\n            return chinaDatas.find(v => v[0].name === params.data.name)[0].value;\r\n          } else {\r\n            return '';\r\n          }\r\n        }\r\n\r\n        var series = [];\r\n        [['湖南', chinaDatas]].forEach(function(item, i) {\r\n          // console.log('item', item);\r\n          series.push({\r\n            type: 'effectScatter',\r\n            coordinateSystem: 'geo',\r\n            zlevel: 2,\r\n            // rippleEffect: {\r\n            //   // 涟漪特效\r\n            //   period: 4, // 动画时间，值越小速度越快\r\n            //   brushType: 'stroke', // 波纹绘制方式 stroke, fill\r\n            //   scale: 10 // 波纹圆环最大限制，值越大波纹越大\r\n            // },\r\n            label: {\r\n              normal: {\r\n                show: true,\r\n                position: 'right', // 显示位置\r\n                offset: [5, 0], // 偏移设置\r\n                formatter: function(params) {\r\n                  // 圆环显示文字\r\n                  // return params.data.name + ' ' + chinaDatas.find((v) => v[0].name === params.data.name)[0].value\r\n                  // return params.data.name + ' ' + showProvinceFoo(params.data.name, params)\r\n                  return '标题';\r\n                },\r\n                fontSize: 12\r\n              },\r\n              emphasis: {\r\n                show: true\r\n              }\r\n            },\r\n            symbol: 'circle',\r\n            symbolSize: function(val) {\r\n              return 6; // 圆环大小\r\n            },\r\n            itemStyle: {\r\n              normal: {\r\n                show: false,\r\n                color: '#FFA54F'\r\n                // color: '#3bc7ff'\r\n              }\r\n            },\r\n            data: item[1].map(function(dataItem) {\r\n              // console.log('中国', dataItem);\r\n              return {\r\n                name: dataItem[0].name,\r\n                value: chinaGeoCoordMap[dataItem[0].name].concat([dataItem[0].value])\r\n              };\r\n            })\r\n          });\r\n        });\r\n\r\n        console.log('series', series);\r\n\r\n        var option = {\r\n          title: {\r\n            text: '单位：人',\r\n            top: 0\r\n            // textStyle: {\r\n            //   color: this.$store.state.app.echartStyle.titleColor, // 字体颜色\r\n            //   fontSize: this.$store.state.app.echartStyle.titleFontSize, // 字体大小\r\n            //   fontWeight: this.$store.state.app.echartStyle.titleFontWeight // 字体粗细\r\n            // }\r\n          },\r\n          // 地图位置调整\r\n          layoutCenter: ['50%', '70%'],\r\n          layoutSize: '100%',\r\n\r\n          tooltip: {\r\n            trigger: 'item',\r\n            // backgroundColor: 'rgba(166, 200, 76, 0.82)', /// 背景色\r\n            borderColor: '#FFFFCC', // 背景色\r\n            showDelay: 0,\r\n            hideDelay: 0,\r\n            enterable: true,\r\n            transitionDuration: 0,\r\n            extraCssText: 'z-index:100',\r\n            formatter: function(params, ticket, callback) {\r\n              // 根据业务自己拓展要显示的内容\r\n              var res = '';\r\n              var name = params.name;\r\n              // var value = params.value[params.seriesIndex + 1]\r\n              var value = chinaDatas.find(v => v[0].name === params.data.name)[0].value;\r\n              res = \"<span style='color:#fff;'>\" + name + '</span><br/>数据：' + value;\r\n              if (name) {\r\n                return res;\r\n              } else {\r\n                return '';\r\n              }\r\n            }\r\n          },\r\n          // backgroundColor: '#13297B',/// 背景色 （生效）\r\n          visualMap: {\r\n            // 图例值控制\r\n            min: 1,\r\n            max: 1000000,\r\n            calculable: true,\r\n            show: true,\r\n            color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#00eaff'], // 散点颜色\r\n            // color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#3bc7ff'], // 散点颜色\r\n            textStyle: {\r\n              color: '#fff'\r\n            }\r\n          },\r\n          geo: {\r\n            map: 'china',\r\n            zoom: 1.42,\r\n            label: {\r\n              emphasis: {\r\n                show: false\r\n              }\r\n            },\r\n            roam: true, // 是否允许缩放\r\n            itemStyle: {\r\n              normal: {\r\n                color: '#13297B', // 地图背景色\r\n                borderColor: '#00fcff', // 省市边界线00fcff 516a89\r\n                borderWidth: 0.5\r\n              },\r\n              emphasis: {\r\n                color: '#13297B' // 悬浮背景\r\n              }\r\n            }\r\n          },\r\n          series: series\r\n        };\r\n\r\n        // end\r\n        const myChart = this.$echarts.init(document.getElementById(this.eleId));\r\n        echarts.registerMap('china', chinaMapJson);\r\n        myChart.setOption(option);\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .chinaMapCls {\r\n    height: 100%;\r\n    // color:#ffde00\r\n  }\r\n</style>\r\n";