export default "<template>\r\n  <div class=\"echart-unit\" :id=\"eleId\"></div>\r\n</template>\r\n\r\n<script>\r\n  import * as echarts from 'echarts';\r\n  import gcoord from 'gcoord';\r\n  import mapGeoJson from '@/views/EchartUnit/map/mapJson/hunan/永州/新田县.json';\r\n  import axios from 'axios';\r\n  const daoxianPoint = [\r\n    { name: '门楼下瑶族乡', value: [112.20415550000001, 26.046486] },\r\n    // { \"name\": \"石羊镇\", \"value\": [ 112.243038, 25.6918935 ] },\r\n    { name: '石羊镇', value: [112.2428615, 25.736837] },\r\n    { name: '陶岭乡', value: [112.329818, 25.7322175] },\r\n    { name: '新隆镇', value: [112.3605475, 25.7736185] },\r\n    { name: '高山乡', value: [112.31385850000001, 25.788556] },\r\n    { name: '三井乡', value: [112.2172915, 25.793779999999998] },\r\n    { name: '毛里乡', value: [112.136879, 25.859922500000003] },\r\n    { name: '龙泉镇', value: [112.206541, 25.9239425] },\r\n    // { \"name\": \"冷水井乡\", \"value\": [ 112.09245849999999, 25.919977 ] },\r\n    { name: '冷水井乡', value: [112.12054599999999, 25.942954] },\r\n    { name: '莲花乡', value: [112.2758015, 25.955085500000003] },\r\n    { name: '新圩镇', value: [112.274978, 25.8029935] },\r\n    { name: '大坪塘乡', value: [112.28057000000001, 25.855258] },\r\n    { name: '金陵镇', value: [112.2869895, 26.0398875] },\r\n    { name: '知市坪乡', value: [112.348353, 25.862666] },\r\n    { name: '金盆圩乡', value: [112.1690135, 25.7108195] },\r\n    { name: '骥村镇', value: [112.1548715, 25.9986065] },\r\n    { name: '枧头镇', value: [112.1581795, 25.8034155] },\r\n    { name: '茂家乡', value: [112.2040195, 25.845184] },\r\n    // { \"name\": \"十字乡\", \"value\": [ 112.060614, 25.8143185 ] },\r\n    { name: '十字乡', value: [112.108503, 25.727265] }\r\n  ];\r\n  export default {\r\n    data() {\r\n      return {\r\n        eleId: `eleId_${Math.random()\r\n          .toString()\r\n          .substring(3, 6) + Date.now().toString(36)}`,\r\n        chart: null,\r\n        option: {\r\n          grid: {\r\n            // 柱状图位置\r\n            top: 30,\r\n            left: 40,\r\n            right: 20,\r\n            bottom: 30\r\n          },\r\n          xAxis: {\r\n            type: 'category',\r\n            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']\r\n          },\r\n          yAxis: {\r\n            type: 'value'\r\n          },\r\n          series: [\r\n            {\r\n              data: [120, 200, 150, 80, 70, 110, 130],\r\n              type: 'bar'\r\n            }\r\n          ]\r\n        }\r\n      };\r\n    },\r\n    mounted() {\r\n      //   this.initData();\r\n      this.initEchart(mapGeoJson);\r\n      this.dataDealFoo(mapGeoJson);\r\n    },\r\n    methods: {\r\n      initData() {},\r\n      initEchart(geoJson) {\r\n        let myChart = echarts.init(document.getElementById(this.eleId));\r\n        const mapName = 'LXK';\r\n        echarts.registerMap(mapName, geoJson);\r\n        //\r\n        let tempData = daoxianPoint;\r\n        let option1 = {\r\n          visualMap: {\r\n            show: false,\r\n            min: 0,\r\n            max: 500,\r\n            left: 'left',\r\n            top: 'bottom',\r\n            text: ['高', '低'], // 文本，默认为数值文本\r\n            calculable: true,\r\n            seriesIndex: [1],\r\n            inRange: {}\r\n          },\r\n          geo: {\r\n            map: mapName,\r\n            show: true,\r\n            roam: true,\r\n            zoom: 1.5,\r\n            label: {\r\n              normal: {\r\n                show: false\r\n              },\r\n              emphasis: {\r\n                show: false\r\n              }\r\n            },\r\n            itemStyle: {\r\n              show: true,\r\n              normal: {\r\n                label: { show: true },\r\n                areaColor: 'rgba(0, 0, 0, 0.1)',\r\n                borderColor: '#2592f6', // '#99CCFF',\r\n                borderWidth: 2\r\n              },\r\n              emphasis: {\r\n                areaColor: '#2592f6'\r\n              }\r\n            }\r\n          },\r\n          series: [\r\n            // {\r\n            //   name: '地图',\r\n            //   type: 'map',\r\n            //   roam: true,\r\n            //   mapType: mapName,\r\n            //   geoIndex: 0,\r\n            //   label: {\r\n            //     normal: {\r\n            //       textStyle: {\r\n            //         fontSize: 10,\r\n            //         fontWeight: 'bold',\r\n            //         color: '#2592f6'\r\n            //       }\r\n            //     }\r\n            //   },\r\n            //   aspectScale: 0.75,\r\n            //   // zoom: 3,\r\n            //   itemStyle: {\r\n            //     normal: {\r\n            //       label: { show: true },\r\n            //       areaColor: 'rgba(0, 0, 0, 0.1)',\r\n            //       borderColor: '#2592f6', // '#99CCFF',\r\n            //       borderWidth: 2\r\n            //     },\r\n            //     emphasis: {\r\n            //       areaColor: '#2592f6',\r\n            //       label: {\r\n            //         color: '#fff'\r\n            //       }\r\n            //     }\r\n            //   }\r\n            // },\r\n            {\r\n              name: '散点',\r\n              type: 'effectScatter',\r\n              coordinateSystem: 'geo',\r\n              legendHoverLink: true,\r\n              // zoom: 3,\r\n              //   symbolSize: function(val) {\r\n              //     return Math.max(val[2] / 30, 8);\r\n              //   },\r\n              label: {\r\n                show: true,\r\n                offset: [10, -10], //偏移设置\r\n                fontSize: 12,\r\n                // lineHeight: 14,\r\n                // borderWidth: 1,\r\n                // backgroundColor: '#061a4c',\r\n                // borderColor: '#97b5f0',\r\n                // padding: 6,\r\n                formatter(value) {\r\n                  // console.log(value);\r\n                  return value.data.name;\r\n                },\r\n                color: '#fff'\r\n              },\r\n              itemStyle: {\r\n                normal: {\r\n                  color: '#f4ea2a', //标志颜色\r\n                  shadowBlur: 4,\r\n                  shadowColor: '#D8BC37'\r\n                }\r\n              },\r\n              data: tempData,\r\n              showEffectOn: 'render',\r\n              rippleEffect: {\r\n                brushType: 'stroke'\r\n              },\r\n              hoverAnimation: true,\r\n              zlevel: 1\r\n            }\r\n          ]\r\n        };\r\n        //\r\n        myChart.setOption(option1);\r\n        //\r\n        myChart.off('click');\r\n        //\r\n        myChart.on('click', params => {\r\n          console.log('点击', params);\r\n        });\r\n      },\r\n      // 数据处理\r\n      dataDealFoo(geoJson) {\r\n        let list = geoJson.features;\r\n        // let obj = { name: '', pint: [] };\r\n        const pointArr = [];\r\n        list.map(item => {\r\n          let arrX = [];\r\n          let arrY = [];\r\n          let arr = item.geometry.coordinates[0];\r\n          let name = item.properties.name;\r\n          // console.log('新田县',item)\r\n          // console.log('新田县2',item.properties)\r\n          // console.log('新田县3',item.properties.name)\r\n          arr.map(item => {\r\n            arrX.push(item[0]);\r\n            arrY.push(item[1]);\r\n          });\r\n          // console.log('arrx', arrX);\r\n          let x = (Math.min(...arrX) + Math.max(...arrX)) / 2;\r\n          let y = (Math.min(...arrY) + Math.max(...arrY)) / 2;\r\n          let result = gcoord.transform([x, y], gcoord.WGS84, gcoord.BD09);\r\n          // console.log('name', name, result);\r\n          pointArr.push({ name: name, value: [x, y] });\r\n          // console.log('新田县-name', { name: name, value: [x, y] }); // 这个地址，准确一些。\r\n        });\r\n        console.log('pointArr', pointArr);\r\n      }\r\n    }\r\n  };\r\n</script>\r\n\r\n<style lang=\"scss\" scoped>\r\n  .echart-unit {\r\n    height: 100%;\r\n  }\r\n</style>\r\n";